.el {
    width: 100px;
    height: 100px;
    color: #886;
    border: 2px solid #886;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
    margin: 5px;
    background: black;
}

.el:hover {
    -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
            transform: scale(1.5);
    width: 150px;
    -webkit-transition: all .8s;
    -o-transition: all .8s;
    transition: all .8s;
    color: #26e81b;
    border: 1px solid #26e81b;
    cursor: pointer;
    -webkit-box-shadow: 0 0 1rem #ffc600;
            box-shadow: 0 0 1rem #ffc600;
    position: relative;
}

.el-main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.el .el-main .el-symbol {
    font-size: 2.5rem;
    margin-right: 5px;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.el:hover .el-main .el-symbol {
    -webkit-transform: scale(1.4);
        -ms-transform: scale(1.4);
            transform: scale(1.4);
    color: #26e81b;
    -webkit-transition: all .8s;
    -o-transition: all .8s;
    transition: all .8s;
    margin-top: 4px;
    margin-right: 14px;
}

.el:hover .el-main .el-id {
    margin-left: 10px;
    margin-top: 6px;
    -webkit-transition: all .8s;
    -o-transition: all .8s;
    transition: all .8s;
    color: #26e81b;
    opacity: 1;
    font-size: 2rem;
}

.el .el-main .el-id {
    font-size: 1rem;
    -webkit-transition: all .8s;
    -o-transition: all .8s;
    transition: all .8s;
    opacity: 0;
}

.el-details {
    margin-left: 3px;
    opacity: 0;
    margin-left: 7px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.el:hover .el-details {
    opacity: 1;
}


