.App {
  background-color: #222;
  padding: 50px;
  padding-top: 20px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  display: -ms-flexbox;
  display: flex;
  color: #61dafb;
  padding-bottom: 20px;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.elements-list {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(10, 100px);
  padding-left: 0;
  grid-gap: 1.5%;
  grid-row-gap: 5px;
}

:nth-child(2) {
  grid-column: 8/9;
}
:nth-child(3), :nth-child(11), :nth-child(19),:nth-child(37) {
  grid-column: 1/2;
}
:nth-child(29) {
  grid-column: 1/2;
}
:nth-child(47) {
  grid-column: 1/2;
}

:nth-child(28):hover, :nth-child(46):hover, :nth-child(56):hover, :nth-child(66):hover, :nth-child(76):hover, :nth-child(86):hover, :nth-child(96):hover, :nth-child(106):hover, :nth-child(116):hover {
  -webkit-transform: scale(1.5) translateX(-20px) !important;
      -ms-transform: scale(1.5) translateX(-20px) !important;
          transform: scale(1.5) translateX(-20px) !important
}

.copy {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  font-size: 1.5rem;

}
.copy-link {
  text-decoration: none;
  color: #61dafb;
}

.container {
  margin: auto;
}


